.app{
    height: 100vh;
}
.sections{
    width: 100%;
    height: calc(100vh - 70px);
    background-color: white;
    position: relative;
    top:70px;
    overflow: scroll;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
}
.sections > *{
width:100vw;
height:calc(100vh - 70px);
scroll-snap-align: start;
}